<template>
    <div class="form-group">
        <input type="checkbox" :value="category.name" :id="'category-' + category.id" v-model="category.checked" @change="updateFilter">
        <label :for="'category-' + category.id" :class="{active : category.checked}">
            {{category.text}}
        </label>
    </div>
</template>

<script>
	import { mapGetters } from 'vuex';

	export default {
		props: ['category'],
        methods: {
	        updateFilter() {
               let checkedIds = [];
		       this.checked.forEach(item => checkedIds.push(item.id));
		       this.$router.push({ name: 'movies', query: { cat: checkedIds } });
            }
        },
        computed: {
	        ...mapGetters({
		        checked: 'checkedCategories'
	        })
        },
	}
</script>

<style scoped lang="scss">

    .form-group {
        margin-bottom: 10px;
    }

    label {
        position: relative;
        min-height: 20px;
        font-size: 13px;
        line-height: 20px;
        padding-left: 30px;
        display: flex;
        align-items: center;
        cursor: pointer;

        &::before {
            content: "";
            display: inline-block;
            vertical-align: sub;
            width: 18px;
            height: 18px;
            border: 1px solid;
            position: absolute;
            left: 0;
            top: 0;
        }


        &.active {
            &::before {
                background-size: 16px;
                background-image: url(data:image/svg+xml;utf8;base64,);

            }
        }
    }

    input {
        position: absolute;
        width: 0;
        height: 0;
        line-height: 0;
        visibility: hidden;
        z-index: -1;
    }



</style>