<template>
    <div class="filter-wrap">
        <div :class="['filter-item', isOpen ? 'open' : '']" @click="isOpen = !isOpen">
            <span> {{dropdownText}}</span>
        </div>
        <div class="dropdown" v-show="isOpen">

            <filter-input
                    v-for="category in categories"
                    :key="category.id"
                    :category="category"
                    v-on:gatherData="$emit('returnToForm')"
            >
            </filter-input>
        </div>
    </div>
</template>

<script>
	import FilterInput from './FilterInput.vue';
	import { mapGetters, mapState } from 'vuex';

	export default {
		data() {
			return {
				isOpen: false,
			}
		},
		computed: {
			dropdownText() {
				let checked = this.checkedCategories;
				if (checked.length === 1) {
					return checked[0].text;
				} else if (checked.length > 1) {
					return checked.length + ' параметра';
				} else {
					return 'Выбрать';
				}
			},

			...mapState([
				'categories'
            ]),

            ...mapGetters([
                'checkedCategories'
            ]),
        },

		components: {
			FilterInput: FilterInput
		}
	}
</script>

<style lang="scss">
    
    .dropdown {
        position: absolute;
        padding: 10px 20px;
        width: 100%;
        background: rgb(215, 215, 215);
    }

    .filter-wrap {
        z-index: 2;
        width: 300px;
        position: relative;
        margin: 0 0 40px;
    }

    .filter-item {
        width: 100%;
        height: 40px;
        border: 1px solid #d7d7d7;
        padding: 10px 20px;
        font-size: 12px;
        text-transform: uppercase;
        position: relative;
    }

    .filter-item::after {
        position: absolute;
        opacity: .5;
        display: block;
        margin-left: 10px;
        height: 16px;
        width: 16px;
        background-repeat: no-repeat;
        transition: .2s;
        content: "";
        top: 30%;
        right: 20px;
        background-size: .8rem;
        -webkit-transform: rotate(90deg);
        transform: rotate(0deg);
        background-image: url(data:image/svg+xml;utf8;base64,);

    }

    .filter-item.open::after {
        transform: rotate(-180deg);
        top: 25%;
    }
</style>